.currentWeatherContainer {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 28vh;
  margin-top: 5vh;

  p {
    font-weight: bold;
    text-align: center;
  }

  ul {
    padding: 0;

    li {
      list-style-type: none;
      margin: 10px 0;
      font-weight: bold;

      p {
        margin: 0;
      }
    }

    div {
      display: flex;
    }
  }

  hr {
    width: 75vw;
    margin-top: 3vh;
  }

  img {
    width: 50px;
  }
}


.weatherDiv {
  width: 90vw;
}

.weatherDivData {
  text-align: end;

  .conditionDiv {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .weatherDivDataTempOverride {
    justify-content: space-around;
    width: 30vw;
    margin: 0 30vw;
  }
}

.weatherCached {
  background-color: hsl(97, 100%, 41%);
  padding: 5px;
  border-radius: 10px;
}